.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

* {
  margin: 0;
  padding: 0;
}

.screenW {
  width: 100vw;
}
.fullW {
  width: 100%;
}
.screenH {
  height: 100vh;
}
.fullH {
  height: 100%;
}
.imgCover {
  object-fit: cover;
}
.overflowH {
  overflow: hidden;
}
.transition1l {
  transition: left 1s ease-in-out;
}
.abs {
  position: absolute;
}
.rel {
  position: relative;
}
.left0 {
  left: 0px;
}
.left100vw {
  left: 100vw;
}
.leftM100vw {
  left: -100vw;
}

.col {
  display: flex;
  flex-direction: column;
}
.flex8 {
  flex: 8;
}